
* {
  margin: 0;
  padding: 0;
}
html,
body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  font-size: 26px;
}
img {
  width: 100%;
  height: 100%;
  vertical-align: top;
}
input {
  border: none;
  outline: none;
}
#app {
  height: 100%;
}
/*页眉*/
.vux-header {
  height: 77px;
  padding-top: 10px !important;
  background: #e53e30 !important;
}
.vux-header .left {
  width: 100px;
}
.vux-header .vux-header-title {
  height: 55px !important;
}
.vux-header .form {
  width: 400px;
  height: 55px;
  padding-left: 50px;
  border-radius: 15px;
}
.vux-header .form input {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  font-size: 26px;
}
.vux-header .right {
  width: 50px;
  height: 50px;
}
/*页脚*/
.weui-tabbar {
  height: 75px;
  background: #e6e2e7 !important;
}
.weui-tabbar .weui-tabbar__item.vux-tabbar-simple {
  height: 100%;
}
.weui-tabbar .weui-tabbar__item.vux-tabbar-simple .weui-tabbar__label {
  height: 100%;
  line-height: 75px;
  font-size: 26px;
  color: #666;
}
.weui-tabbar .weui-tabbar__item.weui-bar__item_on .weui-tabbar__label {
  color: red;
}
